*{
    margin: 0;
    padding: 10px;
}
.box{
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
    min-height:89.8vh;
}
.btn{
    color: white;
    margin:0.3rem;
    padding:0.3rem;
    border-radius: 1rem;
    background-color: black;
    cursor: pointer;
}
.WBbtn{
    position: absolute;
    top: 0.5rem;
    left: 0.5rem;
    padding: 0.2rem;
    border-radius: 0.5rem;
    cursor: pointer;
}