html {
    font-size: 1.25vw;
  }
  
  * {
    margin: 0;
    padding: 0;
  }
  
  /* heading */
  h1 {
    padding: 0.5rem;
    font-size: 3rem;
    text-align: center;
  }
  
  /* navbar */
  .navSection {
    display: flex;
    background-color: rgba(0, 0, 0, 0.753);
  }
  
  .navList {
    list-style-type: none;
    display: flex;
    padding: 0.5rem;
    align-items: center;
    justify-content: center;
  }
  
  .navbar {
    flex-grow: 2;
  }
  
  .navItems,
  .navItemsPhone {
    text-decoration: none;
    transition: color 3s;
    color: yellow;
  }
  
  .navItems li,
  .navItemsPhone li {
    padding: 1rem;
    font-size: 1.5rem;
    font-weight: bold;
    transition: background-color 3s;
    border-radius: 50%;
    text-align: center;
  }
  
  .navItems:hover {
    border-radius: 50%;
    background-color: rgba(105, 105, 105, 0.76);
    color: rgb(255, 48, 48);
  }
  
  .navItemsPhone:hover {
    color: rgb(255, 48, 48);
    background-color: rgba(105, 105, 105, 0.76);
  }
  
  .navbarPhone {
    background-color: black;
  }
  
  /*--------------------- search ---------------------*/
  #searchDiv {
    padding: 1.27rem;
    height: max-content;
    align-items: center;
    justify-content: center;
  }
  
  .search {
    --x: 0.7rem;
    float: left;
    padding: 0.3rem;
    font-size: 1.1rem;
    display: block;
    position: relative;
    margin: auto;
  }
  
  #searchBtn {
    border-top-right-radius: var(--x);
    border-bottom-right-radius: var(--x);
    color: rgb(231, 227, 227);
    background-color: rgb(49, 49, 49);
    font-weight: bold;
  }
  
  #searchBtn:hover {
    color: orange;
    transition: color 2s;
    background-color: rgb(32, 25, 25);
  }
  
  #searchBox {
    border-top-left-radius: var(--x);
    border-bottom-left-radius: var(--x);
    background-color: whitesmoke;
    color: black;
    width: 15vw;
  }
  
  /* Burger */
  
  .line {
    width: 33px;
    margin: 3px 3px;
    height: 4px;
    margin: 1rem;
    background-color: white;
  }
  
  .burger {
    display: none;
    align-items: center;
  }
  
  #myLinks {
    display: none;
  }
  
  @media (max-width: 1000px) {
    html {
      font-size: 1.5vw;
    }
  }
  
  @media (max-width: 800px) {
    html {
      font-size: 2vw;
    }
    .navbar {
      display: none;
    }
    .burger {
      display: block;
      cursor: pointer;
    }
    #myLinks {
      transition: display 3s ease-out;
    }
    h1 {
      flex-grow: 2;
    }
  }
  @media (max-width: 400px) {
    html {
      font-size: 2.3vw;
    }
  }
  @media (max-width: 300px) {
    html {
      font-size: 3vw;
    }
    .navSection {
      flex-direction: column;
    }
    #searchBox {
      width: 70vw;
    }
    .line {
      width: 30vw;
      height: 0.5vh;
    }
    .burger {
      margin: auto;
      align-items: center;
      justify-content: center;
    }
  }
  
  @media (max-width: 200px) {
    html {
      font-size: 5vw;
    }
  }
  